@import url(https://fonts.googleapis.com/css?family=Lato:300i,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto); }
  .wrapper .one {
    grid-column: 1/4;
    grid-row: 1; }
  .wrapper .two {
    grid-column: 1 / 3;
    grid-row: 2/4; }
  .wrapper .three {
    grid-column: 1/3;
    grid-row: 3 / 5; }
  .wrapper .four {
    grid-column: 1/4;
    grid-row: 4; }

body {
  margin: 0px;
  font-family: 'Lato', sans-serif; }

ul {
  list-style-type: none;
  margin: 0px;
  -webkit-margin-before: 0px;
          margin-block-start: 0px;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px; }

.container {
  width: 80%;
  margin: auto; }

.d-flex {
  display: flex;
  justify-content: space-between; }

.w-50 {
  width: 50%; }

.app-header {
  color: white;
  background-image: url(/static/media/background-footer.0b096386.svg); }
  .app-header ul {
    list-style-type: none; }
  .app-header .left {
    width: 30%; }
  .app-header .right {
    display: inline-flex; }
  .app-header .right li {
    padding: 5px 15px;
    align-self: center; }
  .app-header .right li {
    padding: 5px 15px; }
  .app-header .App-link {
    color: #61dafb; }

.app-container .notice {
  background-color: #EBEBEB;
  padding: 10px 30px;
  margin-top: 10px;
  display: flex;
  color: #4F4F4F; }
  .app-container .notice .col {
    flex: 1 1; }
  .app-container .notice .first {
    min-width: 200px; }
  .app-container .notice .title {
    font-size: 20px; }
  .app-container .notice .bold {
    font-weight: 900;
    font-size: 30px;
    color: #333333; }
  .app-container .notice .detail {
    margin-top: 1.6rem; }
  .app-container .notice .close {
    min-width: 30px;
    align-self: center; }
    .app-container .notice .close span {
      vertical-align: middle; }

.app-container .cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: ". ." ". ."; }

.app-container .card {
  padding: 10px; }

.app-container .footer-container {
  margin-top: 20px;
  background-image: url(/static/media/people.f669a2e7.jpg);
  font-size: 2rem;
  padding: 20px 30px;
  display: flex;
  justify-content: space-between;
  background-size: cover;
  margin-bottom: 40px; }
  .app-container .footer-container button {
    background: none;
    border: solid 2px black;
    padding: 5px 20px;
    font-weight: bold;
    font-size: 1.2rem; }

.app-card {
  background-size: contain;
  border: solid 1px transparent;
  max-width: 600px;
  max-height: 800px;
  color: white;
  background-repeat: no-repeat; }
  .app-card .name {
    margin-top: 300px; }
    .app-card .name .vote {
      display: inline-block; }
    .app-card .name .label {
      display: inline-block;
      padding-left: 5px; }
    .app-card .name h2 {
      margin-bottom: 10px;
      font-size: 2rem;
      font-weight: 500; }
  .app-card.cristina {
    background-image: url(/static/media/cristina.2e72ac89.png); }
  .app-card.kaney {
    background-image: url(/static/media/kaney.9192b937.png); }
  .app-card.mark {
    background-image: url(/static/media/mark.25c1dc3c.png); }
  .app-card.malala {
    background-image: url(/static/media/malala.16a48d1b.png); }
  .app-card .caption {
    margin-left: 40px;
    width: 70%; }
  .app-card .date span {
    font-weight: bold; }
  .app-card .detail a {
    margin-right: 10px;
    color: white; }
  .app-card .border {
    border: solid 3px white; }
  .app-card .like,
  .app-card .dislike {
    background-color: #1EBBB4;
    display: inline-block;
    padding: 5px; }
    .app-card .like img,
    .app-card .dislike img {
      width: 25px; }
  .app-card .dislike {
    background-color: #FFAD1C;
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg); }
  .app-card .vote-btn {
    border: solid 1px white;
    background: none;
    min-height: 25px;
    padding: 10px;
    margin-top: -15px; }
  .app-card .percent {
    margin-top: 30px;
    display: flex; }
    .app-card .percent div {
      height: 50px;
      display: flex;
      padding: 10px; }
    .app-card .percent img {
      display: flex;
      align-self: center; }
    .app-card .percent span {
      align-self: center;
      font-size: 2rem;
      padding: 10px; }
  .app-card .rate-like {
    background-color: rgba(21, 133, 128, 0.7);
    text-align: left;
    display: flex; }
  .app-card .rate-dislike {
    text-align: right;
    background-color: rgba(180, 122, 21, 0.7);
    display: flex;
    align-self: center;
    flex-direction: row-reverse; }
    .app-card .rate-dislike img {
      -webkit-transform: rotateY(-180deg) rotateX(-180deg);
              transform: rotateY(-180deg) rotateX(-180deg); }

.app-footer {
  border-top: 1px dotted #ccc;
  padding: 30px 0px; }
  .app-footer li {
    padding-right: 15px; }
  .app-footer a {
    color: black;
    text-decoration: none;
    font-size: 0.8rem; }

.background-hero {
  background-image: url(/static/media/pope1.704e1733.png);
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 600px; }
  .background-hero .justify-center {
    justify-content: center; }
  .background-hero .vote-pope {
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    width: 50%;
    margin: auto; }
    .background-hero .vote-pope .descript {
      padding: 30px; }
    .background-hero .vote-pope h2 {
      font-size: 3.5rem;
      margin: 0; }
    .background-hero .vote-pope p {
      font-size: 1.3rem; }
    .background-hero .vote-pope a {
      color: white; }
    .background-hero .vote-pope .vote img {
      padding: 20px; }
    .background-hero .vote-pope .vote > div {
      text-align: center;
      display: inline-block; }
    .background-hero .vote-pope .like {
      background: rgba(30, 187, 180, 0.6); }
    .background-hero .vote-pope .dislike {
      background: rgba(255, 173, 28, 0.6);
      text-align: center; }
      .background-hero .vote-pope .dislike img {
        -webkit-transform: rotateY(-180deg) rotateX(-180deg);
                transform: rotateY(-180deg) rotateX(-180deg); }
  .background-hero .closing {
    text-align: right;
    background-color: rgba(0, 0, 0, 0.2);
    color: white;
    align-self: center;
    padding: 20px 10px;
    font-size: 13px; }
  .background-hero .days {
    background-color: rgba(255, 255, 255, 0.8);
    align-self: center;
    padding: 10px 10px;
    font-size: 2rem; }
  .background-hero .align-end {
    align-self: self-end; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .App .background-hero {
    background-size: cover;
    min-height: 300px;
    background-position-x: center; }
  .app-footer > .d-flex {
    display: block;
    justify-content: center; }
  .app-footer .d-flex {
    justify-content: center; }
    .app-footer .d-flex:last-of-type {
      padding-top: 25px; } }

/* Small devices (portrait tablets and large phones, 600px and up) */
/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (max-width: 768px) {
  .app-container .cards {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: "." "."; } }

@media only screen and (min-width: 479px) and (max-width: 768px) {
  .app-card {
    width: 70%;
    background-size: cover;
    background-position-x: right;
    margin: auto; } }

@media only screen and (min-width: 769px) and (max-width: 956px) {
  .card .app-card {
    background-size: cover;
    background-position-x: right; }
    .card .app-card .name h2 {
      font-size: 1.3rem; } }

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (max-width: 992px) {
  .app-card .name {
    margin-top: 150px; }
    .app-card .name h2 {
      font-size: 1.5rem; }
  .background-hero {
    background-size: cover;
    min-height: 400px; }
    .background-hero .vote-pope {
      width: 100%; }
    .background-hero .tri::before {
      top: 394px; }
  .app-container .notice {
    display: block; }
    .app-container .notice .title {
      text-align: center; }
  .app-container .footer-container {
    display: block;
    text-align: center; }
  .container {
    width: 90%; } }

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .App .background-hero {
    background-position-y: center; } }

@media only screen and (min-width: 992px) and (max-width: 1440px) {
  .app-card {
    background-size: cover;
    background-position-x: right; }
    .app-card .name h2 {
      font-size: 1.5rem; } }

@media only screen and (min-width: 1440px) {
  .app-card {
    background-size: cover; } }

